.newsletter {
    padding: 2rem 4rem;
    border: 1px solid var(--color-grey);
    background: var(--color-black);
}

.newsletter-heading {
    text-align: center;
}

.newsletter-input {
    flex-direction: row;
    margin-top: 3rem;
}

.newsletter-input input {
    width: 620px;
    border: 1px solid var(--color-grey);
    border-radius: 5px;
    font-size: 1rem;
    font-family: var(--font-base);
    color: var(--color-grey);
    outline: none;

    margin-right: 2rem;
    padding: 0.75rem 1rem;
    background: var(--color-black);
}

.newsletter-input button {
    width: max-content;
}

@media screen and (max-width: 980px) {
    .newsletter-input {
        flex-direction: column;
        width: 100%;
    }

    .newsletter-input input {
        margin: 0 0 2rem 0;
        width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .newsletter {
        padding: 2rem 0;
        border: none;
    }
}